import { useState } from 'react'

import * as Scroll from 'react-scroll';
import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

import LayerThreeText from './LayerData/LayerThree.json'
import SliderNavigation from './Shared/SliderNavigation'

function SetStrandElement(props){
    const strand = props.strand


    return (
        <input 
            className={`purple f4 pointer bg-white b--none`} 
            value={strand}
            type='button'/>
    )
}

function SetStrand(props){


    return (
        <div style={{position:'fixed', width:'100vw'}}>
            <div className='flex justify-center'>
                <div className='ba mt1 h3 flex items-center bg-white shadow-4 br2 bw1 b--purple' style={{width:'1000px'}}>
                    <div className='flex justify-around' style={{width:'1000px'}}>
                        <SetStrandElement 
                            strand='Number and algebra'/>
                        <SetStrandElement 
                            strand='Measurement and geometry'/>
                        <SetStrandElement 
                            strand='Statistics and probability'/>
                    </div>
                </div>
            </div>
        </div>
    )
}


function LayerThreeRowTextOnly(props){
    const text = props.text
    const containsText = text
    return (
        <>
            {containsText &&
            <div className='mt5 mb5 ml4 mr4 ba pa3 bw1 b--purple br2 shadow-3' style={{height: '200px', width: '250px'}}>
                    <div className='dark-gray f5' style={{}}>
                            {text}
                    </div>
            </div>}
            {containsText === "" &&
            <div className='mt5 mb5 ml4 mr4 pa3' style={{height: '200px', width: '250px'}}>
                    <div className='dark-gray f5' style={{}}>
                            {text}
                    </div>
            </div>}
        </>
    )
}


function LayerThreeC(props){

    const LayerThreeTextFlat = LayerThreeText.flat()
    const numberOfRows = LayerThreeTextFlat.length
    
    var LayerThreeRowsNandA = []
    var LayerThreeRowsMandG = []
    var LayerThreeRowsSandP = []

    for (let i=numberOfRows-1; i>0; i--){
        LayerThreeRowsNandA.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Number and algebra']} index={i}/>)
        LayerThreeRowsMandG.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Measurement and geometry']} index={i}/>)
        LayerThreeRowsSandP.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Statistics and probability']} index={i}/>)
    }

    setTimeout(()=>{window.scrollTo(0,100000)},0)
    

    return (
        <div className='bg-near-white'>
            <div className='flex justify-center'>
                <div className='bg-white shadow-4 b--purple bw2 bl ml5 mr5 ' style={{'max-width': '1024px','min-width':'1024px'}}>
                    <div className='flex justify-center'>
                        <div className=''>
                            {/* <div className='flex justify-around' style={{width:'1000px'}}>
                                <SetStrand />
                            </div> */}
                            <div className='flex flex-row'>
                                <div>
                                    {LayerThreeRowsNandA}
                                </div>
                                <div>
                                    {LayerThreeRowsMandG}
                                </div>
                                <div>
                                    {LayerThreeRowsSandP}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LayerThreeC