import { useState } from 'react'

import * as Scroll from 'react-scroll';
import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

import LayerThreeText from './LayerData/LayerThree.json'
import SliderNavigation from './Shared/SliderNavigation'

function SetStrandElement(props){
    const strand = props.strand
    const viewStrand = props.viewStrand
    const setViewStrand = props.setViewStrand

    let isBold = viewStrand === strand ? 'b' : ''
    console.log(isBold)
    function handleClickStrand(e){
        console.log(e.target.value)
        setViewStrand(e.target.value)
    }

    return (
        <input 
            className={`purple f4 pointer ${isBold} bg-white b--none`} 
            value={strand}
            type='button'
            onClick={handleClickStrand}/>
    )
}

function SetStrand(props){

    const viewStrand = props.viewStrand
    const setViewStrand = props.setViewStrand

    return (
        <div style={{position:'fixed', width:'100vw'}}>
            <div className='flex justify-center'>
                <div className='ba mt1 h3 flex items-center bg-white shadow-4 br2 bw1 b--purple' style={{width:'1000px'}}>
                    <div className='flex justify-around' style={{width:'1000px'}}>
                        <SetStrandElement 
                            strand='Number and algebra'
                            viewStrand={viewStrand}
                            setViewStrand={setViewStrand}/>
                        <SetStrandElement 
                            strand='Measurement and geometry'
                            viewStrand={viewStrand}
                            setViewStrand={setViewStrand}/>
                        <SetStrandElement 
                            strand='Statistics and probability'
                            viewStrand={viewStrand}
                            setViewStrand={setViewStrand}/>
                    </div>
                </div>
            </div>
        </div>
    )
}


function LayerThreeRow(props){
    const text = props.text
    const index = props.index

    return (
        <div className='mt5 mb5 ml4 mr4'>
                <div className='dark-gray f5' style={{}}>
                        {text}
                </div>
        </div>
    )
}


function LayerThreeA(props){

    const LayerThreeTextFlat = LayerThreeText.flat()
    const numberOfRows = LayerThreeTextFlat.length
    const isSmoothScroll = props.isSmoothScroll

    const [viewStrand, setViewStrand] = useState('Number and algebra')
    
    var LayerThreeRows = []
    for (let i=numberOfRows-1; i>0; i--){
        let text = LayerThreeTextFlat[i][viewStrand]
        LayerThreeRows.push(<LayerThreeRow text={text} index={i}/>)
    }

    isSmoothScroll ? setTimeout(()=>{scroll.scrollToBottom({smooth: true, duration: 3000})},0) : setTimeout(()=>{window.scrollTo(0,10000)},0)
    

    return (
        <div className='bg-near-white'>
            <SetStrand 
                viewStrand={viewStrand}
                setViewStrand={setViewStrand}
            />
            <div className='flex justify-center'>
                <div className='bg-white shadow-4 b--purple bw2 bl ml5 mr5 ' style={{'max-width': '1024px','min-width':'1024px'}}>
                    <div className='flex justify-center'>
                        <div className='mt5'>
                            {LayerThreeRows}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default LayerThreeA