import PurpleButton from './PurpleButton'

function Modal(props) {
    const clickOk = props.clickOk;
    const modalTitle = props.modalTitle
    const okButtonText = props.okButtonText
    
    return (
        <div className='ba b--purple pa4 br4 bg-white modal-dim-bg selectedBox shadow-4'>            
                <p className='modal-title purple b f4 mb4'>{modalTitle}</p>
            <div className='ml2 dark-gray mb4'>
                {props.children}
            </div>
            <div className="flex flex-row-reverse modal-ok-button" onClick={clickOk}>
                <PurpleButton buttonText={okButtonText}></PurpleButton>
            </div>
        </div>
    );
}

export default Modal;