import { useState , useEffect } from 'react'

import * as Scroll from 'react-scroll';
import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

import LayerThreeText from './LayerData/LayerThree.json'
import Modal from './Shared/Modal'
import SliderNavigation from './Shared/SliderNavigation'

function SetStrandElement(props){
    const strand = props.strand


    return (
        <input 
            className={`purple f4 pointer bg-white b--none`} 
            value={strand}
            type='button'/>
    )
}


function LayerThreeRowTextOnly(props){
    const text = props.text
    const containsText = text
    const handleElementClick = props.handleElementClick

    return (
        <>
            {containsText &&
            <div className='mt5 mb5 ml4 mr4 ba pa3 bw1 b--purple br2 shadow-3 pointer' onClick={handleElementClick} style={{height: '230px', width: '250px'}}>
                    <div className='dark-gray f5' style={{}}>
                            {text}
                    </div>
            </div>}
            {containsText === "" &&
            <div className='mt5 mb5 ml4 mr4 pa3' style={{height: '230px', width: '250px'}}>
                    <div className='dark-gray f5' style={{}}>
                            {text}
                    </div>
            </div>}
        </>
    )
}


function LayerThreeD(props){

    const LayerThreeTextFlat = LayerThreeText.flat()
    const numberOfRows = LayerThreeTextFlat.length
    
    const handleElementClick = props.handleElementClick
    const showModal = props.showModal
    const modalJustClosed = props.modalJustClosed
    const setModalJustClosed = props.setModalJustClosed
    

    const [y, setY] = useState(window.scrollY);

    function handleNavigation(){
        setY(window.scrollY)
    }
    useEffect(() => {
    window.addEventListener("scroll", (e) => handleNavigation(e));

    return () => { // return a cleanup function to unregister our function since its gonna run multiple times
        window.removeEventListener("scroll", (e) => handleNavigation(e));
    };
    }, [y]);

    var LayerThreeRowsNandA = []
    var LayerThreeRowsMandG = []
    var LayerThreeRowsSandP = []

    for (let i=numberOfRows-1; i>0; i--){
        LayerThreeRowsNandA.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Number and algebra']} index={i} handleElementClick={handleElementClick} />)
        LayerThreeRowsMandG.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Measurement and geometry']} index={i} handleElementClick={handleElementClick} />)
        LayerThreeRowsSandP.push(<LayerThreeRowTextOnly text={LayerThreeTextFlat[i]['Statistics and probability']} index={i} handleElementClick={handleElementClick} />)
    }


    return (
        <>
            <div className='b--aubergine bg-purple ba bw1 shadow-4 br1' style={{position:'fixed',top:'30px', right:'30px', width:'300px', height:'60px'}}>
                <div className='f4 flex justify-center'>
                    <div className='mt3'>
                        <div className='white'>
                            {`Level ${12-Math.round((y/32932)*11)}`}  
                        </div>
                    </div>
                </div>
            </div>
            <div className='bg-near-white'>
                <div className='flex justify-center'>
                    <div className='bg-white shadow-4 b--purple bw2 bl ml5 mr5 ' style={{'max-width': '1024px','min-width':'1024px'}}>
                        <div className='flex justify-center'>
                            <div className=''>
                                {/* <div className='flex justify-around' style={{width:'1000px'}}>
                                    <SetStrand />
                                </div> */}
                                <div className='flex flex-row'>
                                    <div>
                                        {LayerThreeRowsNandA}
                                    </div>
                                    <div>
                                        {LayerThreeRowsMandG}
                                    </div>
                                    <div>
                                        {LayerThreeRowsSandP}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default LayerThreeD